.ref-select{position:absolute;z-index:20;top:100%;left:-3px;margin-top:3px;min-width:calc(100% + 6px);background-color:black;color:white;white-space:normal}.ref-option{padding:0 10px;white-space:nowrap;line-height:34px;height:34px}.ref-ghost{background-color:#555}.staggered-transition{-webkit-transition:opacity .5s ease, height .5s ease;transition:opacity .5s ease, height .5s ease;overflow:hidden}.staggered-enter,.staggered-leave{opacity:0;height:0}



